export default ngModule => {
  class FeatureCtrl {
    // @ngInject
    constructor ($http) {
      this.bindMe = 'Hello world';
      $http.get('/data/v1/widgets')
        .then(response => {
          console.log(response.data);
        });
    }
  }

  ngModule.controller('FeatureCtrl', FeatureCtrl);
};


